import React from 'react';
import classes from './Canvas.module.css';

class Canvas extends React.Component {

    constructor(props) {
        super(props);
        this.characters = [];
        this.canvas = '';
        this.ctx = '';
        this.characterList = '';
        this.layes = '';
        this.mouseX = ''
        this.mouseY = '';
        this.rnd = {
            // btwn: function(){
            //     console.log('asdasd');
            // }
        };
    }

    componentDidMount() {
        console.log(document.body.clientHeight);
        this.canvas = this.refs.canvas;
        this.canvas.width = window.innerWidth;
        this.canvas.height = window.innerHeight;

        // console.log(window.innerHeight, window.innerWidth);

        this.ctx = this.canvas.getContext("2d")


        /*Modify options here*/

        //possible characters that will appear
        this.characterList = ['a', 'b', 'c', 'd', 'e', 'f', 'g', 'h', 'i', 'j', 'k', 'l', 'm', 'n', 'o', 'p', 'q', 'r', 's', 't', 'u', 'v', 'w', 'x', 'y', 'z'];

        //stocks possible character attributes
        this.layers = {
            n: 5, //number of layers
            letters: [150, 80, 60, 40, 20], //letters per layer (starting from the deepest layer)
            coef: [0.1, 0.2, 0.4, 0.6, 0.8], //how much the letters move from the mouse (starting from the deepest layer)
            size: [16, 22, 36, 40, 46], //font size of the letters (starting from the deepest layer)
            color: ['#999', '#888', '#777', '#666', '#555'], //color of the letters (starting from the deepest layer)
            font: 'Courier' //font family (of every layer)
        };

        /*End of options*/



        this.mouseX = window.innerWidth / 2;
        this.mouseY = window.innerHeight / 2;

        this.rnd.btwn = (min, max) => {
            return Math.floor(Math.random() * (max - min) + min);
        }
        this.rnd.choose = (list) => {
            return list[this.rnd.btwn(0, list.length)];
        }


        /*ANIMATION*/
        document.addEventListener('mousemove', (ev) => {
            this.mouseX = ev.pageX - this.canvas.offsetLeft;
            this.mouseY = ev.pageY - this.canvas.offsetTop;

            if (window.requestAnimationFrame) {
                window.requestAnimationFrame(this.update.bind(this));
            } else {
                this.update();
            }
        });

        

        this.createLetters();
        this.update();



        /*REAJUST CANVAS AFTER RESIZE*/

        // window.addEventonresize = function () {
        //     location.reload();
        // };
    }

    drawLetter(char) {
        this.ctx.font = char.size + 'px ' + char.font;
        this.ctx.fillStyle = char.color;

        var x = char.posX + (this.mouseX - this.canvas.width / 2) * char.coef;
        var y = char.posY + (this.mouseY - this.canvas.height / 2) * char.coef;

        this.ctx.fillText(char.char, x, y);
    }

    update() {
        this.clear();
        this.drawRender();
    }

    clear() {
        this.ctx.clearRect(0, 0, this.canvas.width, this.canvas.height);
    }

    drawRender() {
        for (let i = 0; i < this.characters.length; i++) {
            this.drawLetter(this.characters[i]);
        }
    }

    /*INITIALIZE*/

    createLetters() {
        // console.log(this.rnd.btwn);
        for (let i = 0; i < this.layers.n; i++) {
            for (let j = 0; j < this.layers.letters[i]; j++) {

                let character = this.rnd.choose(this.characterList);
                let x = this.rnd.btwn(0, this.canvas.width);
                let y = this.rnd.btwn(0, this.canvas.height);

                this.characters.push({
                    char: character,
                    font: this.layers.font,
                    size: this.layers.size[i],
                    color: this.layers.color[i],
                    layer: i,
                    coef: this.layers.coef[i],
                    posX: x,
                    posY: y
                });

            }
        }
    }

    render() {
        return <canvas className={classes.canvas} ref="canvas"/>
    }
}

export default Canvas;